* {
	box-sizing: border-box;
	a:hover {
		text-decoration: none;
	}
}
.desktop-detail {
	margin: 0 5%;
}
.header__product__detail {
	// padding: 20px 0px;
	.header__detail {
		display: flex;
		padding: 0px;
		margin: 30px 2% 50px 2%;
		padding-bottom: 10px;
		border-bottom: 3px solid rgb(43, 40, 40);
		.child__detail:first-child {
			padding-left: 0px;
		}
		.child__detail {
			// padding: 0px;
			// margin: 5px;
			padding-left: 10px;
			padding-right: 10px;
			border-right: 2px solid rgba(63, 60, 60, 0.8);
		}
		.child__detail:last-child {
			// padding: 0px;
			padding-left: 10px;
			padding-right: 0px;
			// margin: 5px;
			// padding-right: 10px;
			font-weight: bold;
			border-right: none;
			text-transform: capitalize;
		}
	}
}

.content__detail {
	width: 100%;
	display: flex;
	justify-content: space-between;
	padding: 0 2%;
	margin-bottom: 5%;
}

.ant-collapse-item {
	font-size: 20px;
}

.ant-collapse-item:active {
	color: f15e2c;
}

.viewed {
	width: 100%;
}

.related {
	margin: auto;
	.header__realated {
		text-align: center;
		font-size: 30px;
		font-family: "Courier New", Courier, monospace;
	}
	.related__slide {
		width: max-content;
		display: flex;
		justify-content: space-between;
		margin: auto;
		img {
			width: 280px;
			height: 280px;
		}
	}
}

.container-evalue {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	.user-evalue {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 50%;
		.anticon {
			svg {
				font-size: 25px;
			}
		}
	}
	.table-evalue {
		// width: 50%;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
		// align-items: center;
		// justify-content: center;
		.rate-evalue {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			margin-right: 50px;
			.anticon {
				svg {
					font-size: 35px;
				}
			}
			.value-rate {
				// width: 50%;
				font-size: 28px;
				font-weight: bold;
			}
		}
		.list-evalute {
		}

		.quality-rate {
			padding-left: 0px;
		}
	}
}
* {
	transition: all 0.03s;
}
