@media only screen and (max-width: 900px) {
	.desktop-detail {
		.container__product__detail {
			.content__detail {
				// margin: 0px 35px;
				display: inline-block;
				.dashed {
					width: 65%;
					margin: auto;
					border-top: double;
				}
				.content__logo {
					margin: 3% auto;
					width: 80%;
					.child__logo {
						.child-side > img {
							width: 100px;
							height: 100px;
						}
					}
				}

				.info__detail {
					width: 70%;
					margin: auto;
				}
			}
		}
	}
}
